import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Card, Row, Table, Col, message } from 'antd';
import FormImportDataStudent from './FormImportDataStudent';
import { createCandidateMassSaveCandidate } from '../../../services/studentCandidate';

const columns = [
  {
    title: 'Họ tên',
    dataIndex: 'id',
    key: 'id',
    render: (text, record) => `${record.firstname} ${record.lastname}`,
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Nơi sinh',
    dataIndex: 'pobId',
    key: 'pobId',
  },
  {
    title: 'Giới tính',
    dataIndex: 'genderText',
    key: 'genderText',
  },
  {
    title: 'HK',
    dataIndex: 'permanentAddress',
    key: 'permanentAddress',
  },
  {
    title: 'Mã trường',
    dataIndex: 'schoolId',
    key: 'schoolId',
  },
  {
    title: 'KV',
    dataIndex: 'areaId',
    key: 'areaId',
  },
  {
    title: 'Năm TN',
    dataIndex: 'graduatedYear',
    key: 'graduatedYear',
  },
  {
    title: 'Hệ ĐT',
    dataIndex: 'trainTypeId',
    key: 'trainTypeId',
  },
  {
    title: 'Mã ngành',
    dataIndex: 'studyFieldId',
    key: 'studyFieldId',
  },
  {
    title: 'Khối',
    dataIndex: 'testGroup',
    key: 'testGroup',
  },
  {
    title: 'Môn 1',
    dataIndex: 'gradeM1',
    key: 'gradeM1',
  },
  {
    title: 'Môn 2',
    dataIndex: 'gradeM2',
    key: 'gradeM2',
  },
  {
    title: 'Môn 3',
    dataIndex: 'gradeM3',
    key: 'gradeM3',
  },
  {
    title: 'ĐTC',
    dataIndex: 'totalGrade',
    key: 'totalGrade',
    render: (text, record) => record.gradeM1 + record.gradeM2 + record.gradeM3,
  },
  {
    title: 'SBD',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'Phương án TS',
    dataIndex: 'esId',
    key: 'esId',
  },
];

class ImportDataStudentPage extends Component {
  state = { lstDataStudent: [] };
  uploadData = lstDataStudent => {
    this.setState({ lstDataStudent });
  };

  cancelUpload = () => {
    this.setState({
      lstDataStudent: [],
    });
  };

  saveDataUpload = async () => {
    const { lstDataStudent } = this.state;
    await createCandidateMassSaveCandidate({ candidates: lstDataStudent });
    message.success('Lưu dữ liệu thành công');
    this.setState({
      lstDataStudent: [],
    });
  };

  render() {
    const { lstDataStudent } = this.state;
    return (
      <Card title="Nhập khẩu dữ liệu">
        <Row>
          <Col span={24}>
            <FormImportDataStudent uploadData={this.uploadData} />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table rowKey="code" bordered dataSource={lstDataStudent} columns={columns} />
          </Col>
          {lstDataStudent.length > 0 && (
            <Col offset={8} span={12}>
              <BtnSave onClick={this.saveDataUpload} />
              <BtnCancel onClick={this.cancelUpload} />
            </Col>
          )}
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ImportDataStudentPage />
  </Page>
);



